@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cambay&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
html {
    scroll-behavior: smooth;
    background-image: url("https://raw.githubusercontent.com/datamesse/datamesse.github.io/main/src/assets-theme/background-tile.png");
    background-attachment: fixed;
}

a {
    word-wrap: break-word;
    font-weight: normal;
}

.body {
    font-family: 'Cambay', Verdana;
    color: #000;
    padding: 0px 12px 36px 12px;
}

.home {
    display: flex;
    flex-direction: column;
    width: 70%;
    margin: auto;
}

.header {
    height: 220px;
    flex: 1 1;
    padding: 20px 12px 10px 12px;
    position: relative;
    align-items: center;
    background-color: #1D87C2;
    background-image: url("https://raw.githubusercontent.com/datamesse/datamesse.github.io/main/src/assets-theme/bannervideo-bluerectangles2-trimmed-7fps.gif");
    background-repeat: no-repeat;
    background-size: cover;
}

.header-body {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0px;
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header h2 {
    padding: 0px;
    margin: 0px;
    margin-bottom: 14px;
    font-family: 'Segoe UI', 'Kanit', Helvetica;
    font-weight: 350;
    font-size: 40px;
}

.header a {
    text-decoration: none;
    color: #FFF;
}

.header-subtitle {
    height: auto;
    width: 100%;
    margin: 90px 0px 0px 0px;
    padding: 0px;
}

.header-subtitle-line {
    width: 70%;
    border-top: 1px #FFF solid;
    margin: 0px;
    margin-right: auto;
    margin-left: auto;
    padding-top: 5px;
}

.header-subtitle-line p {
    margin: 0px;
    padding: 0px 0px 0px 7px;
    color: #FFF;
    font-family: 'Corbel', 'Kanit', Helvetica;
    font-weight: 350;
    font-size: 19px;
    letter-spacing: 0.4em;
    align-items: center;
}

.sitename {
    display: flex;
    font-family: 'Kanit', Helvetica;
    font-size: 30px;
    color: #23a9f2;
    font-weight: 800;
    flex-wrap: nowrap;
}

.sitename a {
    display: flex;
}

.site-letter {
    display: flex;
    height: 22px;
    width: auto;
    margin-right: 4px;
}

.site-title {
    display: flex;
    height: 15px;
    width: auto;
    margin: 0px;
    padding: 0px;
    margin-top: 5px;
    margin-right: 4px;
    margin-bottom: 5px;
}

.site-letter-space {
    margin-right: 13px;
}

.menu-icon {
    height: 35px;
    width: 35px;
    font-size: 24px;
}

.menu-desktop {
    font-family: 'Kanit', Helvetica;
    display: flex;
    flex: 1 1;
    align-items: center;
    justify-content: flex-end;
}

.menu-desktop-option {
    font-size: 14px;
    margin-left: 20px;
    margin-right: 0px;
    font-weight: 700;
    letter-spacing: 0.2em;
}

.menu-desktop-option a {
    text-decoration: none;
    color: #FFF;
    display: flex;
    align-items: center;
    font-weight: bold;
}

.menu-desktop-option a:hover {
    color: #F2F2F2;
}

.menu-option {
    height: 20px;
    width: 20px;
    margin-right: 8px;
}



.menu-mobile {
    font-family: 'Kanit', Helvetica;
    display: none;
}

.close-icon {
    height: 35px;
    width: 35px;
    display: flex;
    position: relative;
    z-index: 20;
}

.menu-mobile-options {
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: #1F201E;
    justify-content: center;
    align-items: center;
    height: 35vh;
    width: 55vw;
    position: absolute;
    right: 0px;
    top: 0px;
    z-index: 1;
    border-radius: 15px;
}

.menu-mobile-option {
    font-size: 16px;
    margin-right: 16px;
    font-weight: 700;
    margin: 12px 0px;
    display: flex;
    letter-spacing: 0.2em;
}

.menu-mobile-option a {
    text-decoration: none;
    color: #FFF;
    display: flex;
    font-weight: bold;
}

.menu-mobile-option a:hover {
    color: #23a9f2;
}


.about {
    margin-bottom: 45px;
}

.about-top {
    display: flex;
    justify-content: space-between;
    align-items: top;
    margin-top: 15px;
    letter-spacing: 0.04em;
    background-color: rgba(60, 64, 67, 0.7);
    -webkit-clip-path: polygon(99% 0, 100% 3%, 100% 100%, 96% 100%, 95% 97%, 88% 97%, 87% 100%, 1% 100%, 0 97%, 0 0);
            clip-path: polygon(99% 0, 100% 3%, 100% 100%, 96% 100%, 95% 97%, 88% 97%, 87% 100%, 1% 100%, 0 97%, 0 0);
}

.about-avatar-outer-border {
    width: 156px;
    height: 156px;
    margin: 0px;
    padding: 0px;
    background-color: #1F201E;
    -webkit-clip-path: polygon(0 0, 70% 0, 100% 30%, 100% 100%, 30% 100%, 0 70%);
            clip-path: polygon(0 0, 70% 0, 100% 30%, 100% 100%, 30% 100%, 0 70%);
}

.about-avatar-mid-border {
    width: 154px;
    height: 154px;
    margin: 1px;
    padding: 0px;
    padding-top: 0.25px;
    background-color: white;
    -webkit-clip-path: polygon(0 0, 70% 0, 100% 30%, 100% 100%, 30% 100%, 0 70%);
            clip-path: polygon(0 0, 70% 0, 100% 30%, 100% 100%, 30% 100%, 0 70%);
}

.about-avatar-inner-border {
    width: 152px;
    height: 152px;
    margin: 1px;
    padding: 0px;
    background-color: #1F201E;
    -webkit-clip-path: polygon(0 0, 70% 0, 100% 30%, 100% 100%, 30% 100%, 0 70%);
            clip-path: polygon(0 0, 70% 0, 100% 30%, 100% 100%, 30% 100%, 0 70%);
}

.about-avatar {
    width: 150px;
    height: 150px;
    margin: 1px;
    padding: 0px;
    -webkit-clip-path: polygon(0 0, 70% 0, 100% 30%, 100% 100%, 30% 100%, 0 70%);
            clip-path: polygon(0 0, 70% 0, 100% 30%, 100% 100%, 30% 100%, 0 70%);
}

.about-avatar-inner-border:hover {
    background-color: #24A9F2;
    background-image: url("https://raw.githubusercontent.com/datamesse/datamesse.github.io/main/src/assets-theme/bannervideo-bluerectangles2-trimmed-7fps.gif");
    background-repeat: no-repeat;
    background-size: cover;
}

.about-avatar-mid-border:hover {
    background-color: #88e8ff;
}

.about-avatar-outer-border:hover {
    background-color: #c6ffff;
}



.about-info {
    width: 70%;
    font-size: 20px;
    padding: 20px 30px 0px 30px;
}

.about-info p {
    line-height: 1.35;
    color: white;
    font-family: 'Cambay', 'Corbel';
    font-size: 18px;
    font-weight: 500;
}

.about-info ul {
    margin-top: 0px;
}

.about-info ul li {
    line-height: 1.75;
    color: white;
    font-family: 'Cambay', 'Corbel';
    font-size: 18px;
    font-weight: 500;
}

.about-title {
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-image {
    height: 350px;
}

.about-quote {
    font-size: 15px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin-top: 7px;
    margin-bottom: 15px;
}

.about a {
    text-decoration: none;
    color: #000;
}

.about-links {
    height: auto;
    width: 650px;
}

.links {
    height: 100%;
    display: flex;
    justify-content: left;
    align-items: center;
    background-color: #F2F2F2;
}

.links ul {
    list-style: none;
    margin: 15px 20px 15px 15px;
    padding: 0px;
}

.links a {
    text-decoration: none;
}

.links a:hover {
    color: #24A9F2;
}

.link {
    padding: 0px;
    cursor: pointer;
    margin: 6px;
    display: flex;
}

.link-icon-container {
    width: 50px;
    padding: 0px;
    margin: 0px;
}

.link-text {
    font-family: 'Segoe UI', 'Kanit', Helvetica;
    font-weight: 400;
    font-size: 20px;
    margin: 3px 0px 0px 5px;
    padding: 0px;
}

.link-icon {
    height: 30px;
}

.home .body .contact .links {
    border: 2px solid #A2A2A2;
    box-shadow: 7px 7px rgba(60, 64, 67, 0.15);
}


.header-subtitle .links {
    height: auto;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background: none;
    margin-bottom: 10px;
}

.header-subtitle .links a:hover {
    color: #F2F2F2;
}

.header-subtitle .links ul {
    margin: 0px;
}

.header-subtitle .links ul li {
    display: inline-flex;
    margin-left: 10px;
    margin-right: 10px;
}

.header-subtitle .link-icon-container {
    width: 28px;
    height: 28px;
    margin-right: 3px;
    padding: 3px;
    border: 1px #FFFFFF solid;
    background-color: rgba(255, 255, 255, 0.4);
}

.header-subtitle .link-icon-container:hover {
    background-color: rgba(255, 255, 255, 0.7);
}

.header-subtitle .link-icon-container img {
    height: 100%;
    width: 100%;
}



.portfolio {
    display: flex;
    flex-direction: column;
    flex: 1 1;
}

.portfolio a h3 {
    color: #24A9F2;
    padding-left: 0px;
}

.project {
    display: flex;
    flex: 1 1;
    justify-content: space-between;
    align-items: center;
}

.project-body {
    margin: 14px 0px 50px;
    padding: 30px 15px 15px 15px;
    background-color: white;
    border: 2px solid #A2A2A2;
    display: flex;
    flex-direction: row;
    box-shadow: 7px 7px rgba(60, 64, 67, 0.15);
}

.portfolio .portfolio-preview .project-shell .project .project-body:hover {
    background-color: #24A9F2;
    background-image: url("https://raw.githubusercontent.com/datamesse/datamesse.github.io/main/src/assets-theme/bannervideo-bluerectangles2-trimmed-7fps.gif");
    background-repeat: no-repeat;
    background-size: cover;
    border: 2px solid #24A9F2;
    -webkit-animation: glowproject 1s infinite alternate;
            animation: glowproject 1s infinite alternate;
}

@-webkit-keyframes glowproject {
    from {
      box-shadow:
      0 0 1px 1px #c6ffff;
    }
    to {
      box-shadow:  
      0 0 1px 2px #acffff,
      0 0 1px 2px #88e8ff;
    }
}

@keyframes glowproject {
    from {
      box-shadow:
      0 0 1px 1px #c6ffff;
    }
    to {
      box-shadow:  
      0 0 1px 2px #acffff,
      0 0 1px 2px #88e8ff;
    }
}

.portfolio .portfolio-preview .project-shell .blog-tile-header .blog-title-header-rectangle a:hover {
    color: #24A9F2;
}

.portfolio .project-shell .blog-tile-header .blog-title-header-rectangle a:hover {
    color: #24A9F2;
}

.project-info {
    margin-top: 6px;
    margin-right: 14px;
}

.project-info p {
    font-size: 19px;
    line-height: 1.45;
    font-weight: 300;
    color: #1D3251;
    border-top: 1px solid #000000;
    margin-top: 0px;
    padding-top: 14px;
}

.project-title {
    font-family: 'Cambay', 'Kanit', Helvetica;
    font-size: 20px;
    font-weight: 500;
    color: #000;
}

.project-links-date {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.project-links {
    display: flex;
    margin-top: 6px;
}

.project-links a {
    text-decoration: none;
    color: black;
}

.project-links a:hover {
    color: #24A9F2;
}

.project-link-button {
    font-family: 'Segoe UI', Helvetica;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    width: -webkit-max-content;
    width: max-content;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 19px;
    margin-right: 6px;
    letter-spacing: 0.1em;
}

.project-link-icon {
    height: 30px;
    display: flex;
    align-items: center;
    margin-right: 10px;
}

.project-date {
    font-family: 'Segoe UI', Helvetica;
    font-size: 18px;
    font-weight: 500;
    text-align: right;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 6px;
    margin-right: 15px;
    color: #1D3251;
}

.project-tags {
    display: flex;
    max-width: 60%;
    flex-wrap: wrap;
}

.project-tag {
    font-family: 'Kanit', Helvetica;
    font-weight: 600;
    font-size: 14px;
    padding: 2px 10px 2px 10px;
    background-color: #D7D8DA;
    margin-right: 6px;
    margin-bottom: 4px;
}

.project-photo a {
    width: 100%;
    margin: 0px;
    padding: 0px;
}

.project-photo {
    border: 1px solid rgba(60, 64, 67, 0.85);
    width: 40vw;
    padding-left: auto;
    Padding-right: auto;
}

.project .blog-tile-header {
    margin: 0px;
    padding: 0px;
}




/* ------- PORTFOLIO PREVIEW -------*/

.portfolio .portfolio-preview {
    display: grid;
    grid-gap: 10px;
    grid-auto-flow: row;
    grid-template-columns: repeat(3, auto);
    margin-bottom: 15px;
}

.portfolio .portfolio-preview .project-shell {
    width: -webkit-max-content;
    width: max-content;
}

.portfolio .portfolio-preview .project-shell .project {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-left: 0px;
}


.portfolio .portfolio-preview .project-shell .project a img {
    width: 20vw;
}

.portfolio .portfolio-preview .project-shell .project .project-body a {
    z-index: 9999;
}

.portfolio .portfolio-preview .project-shell .project .project-body .project-info {
    width: 20vw;
    position: absolute;
}

.portfolio .portfolio-preview .project-shell .project .project-body .project-info .project-title {
    color: white;
}

.portfolio .portfolio-preview .project-shell .project .project-body .project-info p {
    color: white;
    border: none;
}

.portfolio .portfolio-preview .project-shell .project .project-body .project-info .project-links-date {
    display: none;
}

.portfolio .portfolio-preview .project-shell .project .project-body .project-info .project-tags {
    display: none;
}

.portfolio .portfolio-preview .project-shell .project .project-body a .project-photo:hover {
    opacity: 0;
}



.portfolio .portfolio-preview .project-shell .project .project-body {
    margin-bottom: 15px;
    padding: 25px 7px 0px 7px;
}

.portfolio a {
    text-decoration: none;
    color: #000;
    margin-left: auto;
    margin-right: auto;
}

.portfolio h3 {
    font-size: 20px;
    font-family: 'Corbel', 'Kanit', Helvetica;
    font-weight: 500;
    margin-bottom: 40px;
}


/* ----------------------------*/

.section-title {
    display: flex;
    margin: 0px;
    padding: 12px 10px 12px 10px;
    background-color: #1F201E;
    box-shadow: 7px 7px rgb(60, 64, 67, 0.15);
}

.section-title-glow {
    width: 5px;
    height: auto;
    margin-top: 0px;
    margin-left: 9px;
    margin-right: 9px;
    padding-top: 15px;
    padding-bottom: 15px;
    background-color: #f3ffff;
    display: inline-flex;
    -webkit-animation: glowtitle 2s infinite alternate;
            animation: glowtitle 2s infinite alternate;
}

@-webkit-keyframes glowtitle {
    from {
      box-shadow:
      0 0 1px 2px #c6ffff;
    }
    to {
      box-shadow:  
      0 0 1px 2px #acffff,
      0 0 2px 3px #88e8ff;
    }
  }

@keyframes glowtitle {
    from {
      box-shadow:
      0 0 1px 2px #c6ffff;
    }
    to {
      box-shadow:  
      0 0 1px 2px #acffff,
      0 0 2px 3px #88e8ff;
    }
  }


.section-title-text {
    font-family: 'Kanit', Helvetica;
    font-size: 24px;
    letter-spacing: 0.3em;
    font-weight: bold;
    color: white;
    margin: 0px 0px 0px 15px;
    padding: 0px;
}

.portfolio .section-title {
    margin-bottom: 30px;
}

.about .section-title {
    margin-top: 30px;
    margin-bottom: 30px;
}

.blog-preview .section-title {
    margin-top: 50px;
    margin-bottom: 30px;
}

.skills .section-title {
    margin-top: 45px;
    margin-bottom: 30px;
}

.contact .section-title {
    margin-top: 50px;
    margin-bottom: 30px;
}




.section-button {
    width: auto;
    text-align: center;
    margin: 0px auto 0px auto;
    border-radius: 10px;
    background-color: #24A9F2;
    background-image: url("https://raw.githubusercontent.com/datamesse/datamesse.github.io/main/src/assets-theme/bannervideo-bluerectangles2-trimmed-7fps.gif");
    background-repeat: no-repeat;
    background-size: cover;
    border: 1px solid rgba(60, 64, 67, 0.8);
    box-shadow: inset 0 0 6px #c6ffff;
}

.section-button img {
    opacity: 1;
}

a .section-button {
    color: white;
    font-family: 'Kanit', Helvetica;
    font-size: 18px;
    letter-spacing: 0.20em;
    font-weight: bolder;
    padding: 0px 20px 0px 20px;
/*    text-shadow:
        -1px -1px 0 #2E3639,  
         1px -1px 0 #2E3639,
        -1px  1px 0 #2E3639,
         1px  1px 0 #2E3639;  */
}

.section-button:hover {
    border: 1px solid #24A9F2;
    box-shadow: offset 0 0 15px #FFF;
}

a .section-button:hover {
    text-shadow: none;
}


.skills {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.skills-body {
    color: white;
    padding: 20px 20px 20px 10px;
    background-color: rgba(60, 64, 67, 0.7);
    box-shadow: 7px 7px rgb(60, 64, 67, 0.15);
}

.skills a {
    color: white;
    text-decoration: none;
}

.skills a:hover {
    color: #23a9f2;
}

.skills p {
    font-size: 18px;
    margin-top: 8px;
    margin-bottom: 8px;
    line-height: 1.45;
    padding-left: 20px;
    padding-right: 20px;
}

.skills ul li {
    font-size: 18px;
    margin-bottom: 8px;
    padding-left: 20px;
    padding-right: 20px;
}

.skills-section {
    display: flex;
    justify-content: space-between;
    margin: 12px 0px;
    margin-top: 15px;
}

.skills-section-title {
    font-size: 24px;
    font-weight: 500;
    font-family: 'Cambay', 'Kanit', Helvetica;
}

.skills-list {
    display: flex;
    flex-wrap: wrap;
    max-width: 70%;
    justify-content: flex-end;
}



.skill {
    display: flex;
    flex-direction: column;
/*    width: max-content; */
    padding: 8px 16px;
    height: 140px;
    width: 140px;
    align-items: center;
    justify-content: center;
    margin: 6px;
}

.skill-icon {
    height: 60px;
}

.skill-name {
    font-size: 18px;
    font-family: 'Kanit', Helvetica;
    font-weight: 700;
    margin-top: 7px;
}

.skill-score {
    font-size: 12px;
    font-family: 'Kanit', Helvetica;
    font-weight: 600;
    margin: 0px;
}

.skill-date {
    font-size: 11px;
    font-family: 'Kanit', Helvetica;
    font-weight: 600;
    margin: 0px;
}




.skills2-full-list {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
}



/* SKILL BAR version 2 */

.skill2 {
    margin: 0px;
    padding: 0px;
    display: flex;
    font-size: 18px;
}

.skills2-list {
    margin-left: 20px;
}

.skill2-iconcontainer {
    margin: 0px;
    padding: 0px;
    height: 50px;
    width: 50px;
    align-content: center;
}

.skill2 img {
    margin: auto;
    padding: 0px;
    width: 100%;
    height: auto;
    object-fit: fill;
}

.skill-icon2 {
    margin: 0px;
    padding: 0px;
    height: 50px;
    width: 50px;
}

.skill2-bar {
    width: 100%;
    margin: 7px 0px 30px 20px;
    padding: 0px;
}

.skill2-progress-bar {
    display: flex;
    background-color: #E9F6FE;
    margin: 0px;
    padding: 0px;
    width: 100%;
}

.skill2-progress-bar-fill {
    background-color: #24A9F2;
    margin: 0px;
    padding: 0px;
    width: 70%;
    color: #FFF;
    text-align: right;
    padding-right: 10px;
}

.skill2-progress-bar-fill-end {
    width: 0;
    height: 0;
    border-top: 100% solid transparent;
    border-bottom: 100% solid transparent;
    border-left: 100% solid #24A9F2;
    display: none;
}

.skill2-subtext {
    display: flex;
    margin: 0px;
    padding: 0px;
}

.skill2-name {
    margin-right: 5px;
}

.skill2-people {
    margin-left: auto;
}

/* SKILL BAR version 2 END */



.contact {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.contact p {
    font-family: 'Corbel', 'Kanit', Helvetica;
    font-size: 20px;
    margin: auto;
    margin-bottom: 20px;
